@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
html {
  font-size: 62.5%;
}

body {
  background-color: #f3f3f3;
  overflow-x: hidden;
  max-width: 128rem;
  margin: auto;
  font-family: "Poppins", sans-serif;
}

svg {
  vertical-align: -0.5rem;
}

.btn {
  background-color: #009ef7;
  color: #fff;
  margin-bottom: 2px;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 1.4rem;
  -webkit-transition: 0.2s ease-in background-color;
  transition: 0.2s ease-in background-color;
  text-align: center;
  cursor: pointer;
}
.btn:hover {
  background-color: #0493e6;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.header {
  width: 100%;
  max-width: 128rem;
  height: 6.6rem;
  background: #002045;
  padding: 0 2rem;
  color: #fff;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
.header__logo {
  width: 19.6rem;
  height: 4.6rem;
}
.header__search-container {
  width: 40rem;
  position: relative;
}
.header__search-input {
  width: 100%;
  padding: 1.2rem;
  outline: none;
  border: none;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.85);
}
.header__search-icon {
  width: 1.5rem;
  height: 1.6rem;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}
.header__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.header__links li a {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
  font-size: 1.3rem;
}
.header__links-register-icon, .header__links-user-icon {
  width: 2rem;
  height: 2rem;
}

.category {
  height: 8rem;
  background-color: #fff;
  margin: 1px 0;
}

.secondary-category {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: unset;
  padding: 1.5rem;
  gap: 2rem;
}
.secondary-category a {
  color: #1a86f4;
}
.secondary-category__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
  font-size: 1.3rem;
}
.secondary-category__img {
  width: 8.2rem;
  height: 8.2rem;
  border: 1px solid #fff;
  border-radius: 50%;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.carousel {
  max-width: 128rem;
  width: 100%;
}
.carousel__img {
  width: 100%;
}

.offers {
  background-color: #001AFF;
  padding: 6rem 1rem 1rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 4rem;
}
.offers__left {
  width: 25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 6rem;
  text-align: center;
}
.offers__left--text {
  font-size: 4.8rem;
  color: #fff;
  letter-spacing: 2px;
}
.offers__left--img {
  width: 15rem;
  height: 15rem;
}
.offers button {
  font-size: 2rem;
  padding: 1rem 3rem;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 6px;
  text-transform: uppercase;
  outline: none;
}

.card {
  height: 35rem;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  padding: 1.6rem;
  color: #19191D;
}
.card-home {
  width: 19rem;
}
.card__img {
  width: 100%;
  height: 14.4rem;
  object-fit: contain;
}
.card__subtitle {
  font-size: 1.4rem;
  letter-spacing: 1px;
  height: 8rem;
  max-height: 8rem;
  display: block;
  padding: 1.2rem 0 2rem;
  color: #2f2f2f;
}
.card__price {
  font-size: 2.2rem;
  display: block;
  font-weight: bold;
  padding-bottom: 2.5rem;
}
.card__details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__seller-info {
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.card__seller-info svg {
  width: 1.5rem;
  height: 1.5rem;
}
.card__seller-shop {
  margin-bottom: 0.2rem;
}
.card__seller-location {
  display: flex;
  color: #c4c4c4;
  gap: 0.2rem;
}
.card__express-tag {
  font-size: 1.3rem;
  font-weight: bold;
  padding: 0.1rem 0.9rem;
  background-color: #FFB800;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.cards--container {
  display: flex;
  gap: 0.8rem;
}

.product-cards {
  padding: 2rem 0 2rem 2rem;
}

.product-cards__info {
  padding: 0 2rem 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-cards__info-title {
  font-size: 3.4rem;
}

.product-cards__info-link {
  font-size: 1.6rem;
  color: #0156FF;
}

.ad {
  width: 100%;
  max-width: 128rem;
}
.ad__img {
  width: 100%;
}

.fourth-ad,
.sixth-ad {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.fourth-ad__img,
.sixth-ad__img {
  width: 100%;
  object-fit: cover;
}

.main {
  padding: 1rem 2.2rem;
  width: 100%;
}
.main__breadcrumb-container {
  display: flex;
  margin: 1rem 2.2rem 1rem 0;
}
.main__breadcrumb-item {
  color: #808080;
  font-size: 1.5rem;
  margin-right: 0.6rem;
}
.main__breadcrumb-item :hover {
  color: #009ef7;
}
.main__breadcrumb-item::after {
  content: "‚ùØ";
  padding-left: 0.4rem;
}
.main__breadcrumb-item:last-child:after {
  content: "";
}
.main__breadcrumb-item a {
  color: #808080;
}

.grid-container {
  display: grid;
  grid-template-columns: minmax(0, 1fr) 34rem;
  gap: 0 1rem;
}

.store__product-card {
  background-color: #fff;
  padding: 1rem;
  display: flex;
  gap: 3rem;
  border-radius: 4px;
}

.card__left {
  width: 38rem;
  padding: 0 2rem;
}

.store__card-img {
  width: 100%;
  height: 22rem;
  object-fit: contain;
}

.card__view-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.price-container {
  display: flex;
  flex-direction: column;
}

.store__card-price {
  font-size: 1.6rem;
  font-weight: bold;
  color: rgb(215, 57, 72);
}

.off {
  margin-left: 3px;
}

.price {
  text-decoration: line-through;
  font-weight: normal;
}

.card__view {
  font-size: 1.5rem;
  font-weight: bold;
  color: #427ec2;
}

.view-icon {
  padding-right: 0.4rem;
  width: 2rem;
  height: 2rem;
}

.card__right-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1rem;
}
.card__right-top h1 {
  font-size: 2.2rem;
}
.card__right-top--icons {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.wishlist-icon, .compare-icon {
  border: 1px solid #d8d5d5;
  width: 2.8rem;
  height: 2.8rem;
  border-radius: 4px;
  padding: 0.4rem;
  transition: 0.1s ease-in;
  cursor: pointer;
}

.compare-icon:hover {
  color: #50cd89;
  border-color: #50cd89;
}

.wishlist-icon:hover {
  color: rgb(215, 57, 72);
  border-color: rgb(215, 57, 72);
}

.card__right-middle {
  font-size: 1.5rem;
  opacity: 0.92;
  height: 15rem;
}

.card__right-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card__buttons {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.website-btn,
.call-btn {
  background-color: #009ef7;
  color: #fff;
  margin-bottom: 2px;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 1.4rem;
  -webkit-transition: 0.2s ease-in background-color;
  transition: 0.2s ease-in background-color;
  text-align: center;
  cursor: pointer;
}
.website-btn:hover,
.call-btn:hover {
  background-color: #0493e6;
}

.star-icon {
  width: 1.6rem;
  height: 1.6rem;
}

.user-score-title {
  font-size: 1.3rem;
}

.barcode-container {
  flex-basis: 6rem;
}

.barcode-img {
  width: 100%;
  height: 6rem;
  image-rendering: pixelated;
}

.store {
  background-color: #fff;
  max-height: 36rem;
  border-radius: 4px;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
.store__info {
  display: flex;
}
.store__info-img {
  width: 5rem;
  height: 5rem;
  object-fit: contain;
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid rgb(177, 176, 176);
}
.store__info-location {
  color: rgb(196, 196, 196);
  font-size: 1.2rem;
}
.store__info-location svg {
  width: 1.5rem;
  height: 1.5rem;
  vertical-align: -0.3rem;
}
.store__info-name-container {
  display: flex;
  flex-direction: column;
}
.store__info-name {
  color: #000;
  font-size: 1.4rem;
  font-weight: bold;
}
.store__description {
  opacity: 0.92;
  max-height: 220px;
  font-size: 1.4rem;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  -webkit-box-flex: 1;
  flex-grow: 1;
  margin: 1rem 0;
  overflow: hidden;
}
.store__contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.instagram, .whatsapp, .telegram {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
}

.method {
  font-size: 1.2rem;
}
.method label {
  vertical-align: 0.25rem;
}

.other__stores {
  margin: 1rem 0;
  border-radius: 4px;
}
.other__stores-container {
  background-color: #fff;
  border-radius: 4px;
}
.other__stores-card-container {
  padding: 1rem 1rem 0;
}

.other__store-card {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  border-bottom: 1px solid #dfe3e8;
}
.other__store-card-right {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.other__store-card-right-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.other__store-card-right-middle {
  flex-grow: 1;
}
.other__store-card-right-middle p {
  padding: 0.8rem 0;
  font-size: 1.4rem;
  opacity: 0.92;
}
.other__store-card-right-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.other__store-product-img {
  width: 15rem;
  height: 15rem;
  object-fit: contain;
}
.other__store-img {
  margin-left: 1rem;
}
.other__store-btn {
  color: #fff;
  font-size: 1.8rem;
}
.other__store-btn-container {
  background-color: #009ef7;
  color: #fff;
  margin-bottom: 2px;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 1.4rem;
  -webkit-transition: 0.2s ease-in background-color;
  transition: 0.2s ease-in background-color;
  text-align: center;
  cursor: pointer;
  padding: 1rem;
  margin: 1rem;
}
.other__store-btn-container:hover {
  background-color: #0493e6;
}

.other--off {
  margin-right: 1rem;
}

.product__details {
  padding: 1rem 2rem;
  max-height: 115rem;
  overflow-y: auto;
  position: relative;
  background-color: #fff;
}
.product__details-name {
  font-size: 1.8rem;
  border-bottom: 1px solid #c4c4c4;
  padding: 10px 0;
}
.product__details-description {
  font-size: 1.4rem;
  padding: 1rem 0;
  opacity: 0.92;
}

.tabs {
  display: flex;
}
.tabs li {
  padding: 10px 15px 2px;
  font-size: 1.4rem;
  cursor: pointer;
  font-weight: bold;
}

.related__products {
  padding: 1rem 2.2rem;
}
.related__products-title {
  font-size: 1.4rem;
  text-align: center;
  margin-bottom: 1.5rem;
}
.related__products-container {
  gap: 0.8rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(19rem, 1fr));
  justify-content: center;
}

.section__main {
  width: 100%;
  display: flex;
  padding: 10rem 2rem;
  gap: 2rem;
}

.aside {
  user-select: none;
  border-right: 1px solid #000;
  flex-basis: 34rem;
  padding: 1rem 2rem;
  max-height: 100rem;
  overflow-y: auto;
}
.aside__department {
  padding: 1rem 2rem;
  border: 1px solid #9DC2FF;
  color: #2264D1;
  border-radius: 0.8rem;
  margin-bottom: 4rem;
  background-color: #fff;
  text-align: center;
  font-size: 1.8rem;
  font-weight: bold;
  letter-spacing: 1px;
}
.aside__categories-header {
  cursor: pointer;
}
.aside__categories-header span {
  font-size: 1.2rem;
  color: #787885;
}
.aside__categories-container {
  background-color: #fff;
  padding: 1rem 2rem;
  font-size: 1.4rem;
  border-radius: 4px;
  margin-bottom: 2rem;
}
.aside__categories-item {
  margin: 0.6rem 0 0.6rem 0.8rem;
  cursor: pointer;
}
.aside__filters {
  font-size: 1.4rem;
  background-color: #F7F7FA;
  padding: 1rem 2rem;
  border-radius: 4px;
  margin-bottom: 2rem;
}
.aside__filters-header {
  margin-bottom: 2rem;
}
.aside__filters-body {
  padding-left: 1rem;
}
.aside__filters-item {
  margin-bottom: 1rem;
}
.department__icon {
  width: 2rem;
  height: 2rem;
  margin-right: 0.5rem;
}

